/* 可以设置不同的进入和离开动画 */
/* 设置持续时间和动画函数 */
.slide-fade-enter-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.slide-fade-leave-active {
  -webkit-transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
  /* .slide-fade-leave-active for below version 2.1.8 */ {
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
  opacity: 0;
}

.box-body > .comment {
  margin-left: 18px
}
.box-body >.comment>.text-content{
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 190px;
}
.topauthor{
  margin-top: 10px;
  font-size: 1em;
}
.topauthor > .avatar-photo{
  margin: -5px 15px;
  border: 1px solid #CCCCCC;
}
.topauthor .talk{
  padding-top: 8px;
  overflow: hidden;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  width: 160px;
}
.topauthor .tag{
  padding: 5px ;
  background: #0090ce;
  color: #fff;
  margin-right: 6px;
  cursor: pointer;
  font-weight: bold;
}
.butauthor{
  margin-top: 20px;
  color: #999;
  text-align: center;
  background: #f8f8f8;
  float: left;
  font-size: 14px;
  width: 100%;
  padding: 0 10px;
  border-top: 1px solid #ddd;
}
.butauthor > span{
  color: #666;
  float: left;
